@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,400&display=swap);
* {
	margin: 0;
	padding: 0;
	font-family: "Montserrat", sans-serif;
}

:root {
	--white: #f8f8f8;
	--light: #f8f8f8;
	--head-light: #f8f8f8;
	--grey: #232323;
	--gradient-grey: #232323;
	--skill-grey: #272727;
	--theme: #8148eb;
	--dark: #191919;
	--purple: #8e61e2;
	--logo-bg: none;
	--light-purple: #bd6df2;
	--shadow: none;
	--icon-bg: #5555556e;
	--gradient: linear-gradient(134deg, #836af0 0%, #8e49dc 99%);
	--gradient-light: linear-gradient(99deg, #836af0 0%, #8e49dc 130%);
	--input: #f0f0f0;
	--filter: brightness(20%);
}

.light {
	--light: #8949dc;
	--head-light: #313131;
	--grey: #f8f8f8;
	--gradient-grey: var(--gradient);
	--skill-grey: #dfc7ff;
	--dark: #f0ecec;
	--logo-bg: #8e61e2;
	--light-purple: #f8f8f8;
	--shadow: -1px 3px 12px 0px #8b8b8b21;
	--icon-bg: rgba(209, 209, 209, 0.233);
	--input: #eeeeee;
	--filter: contrast(15%);
}

#root {
	color: var(--light);
	background: var(--dark);
	min-height: 100vh;
	transition: 300ms ease-in-out;
}

html {
	scroll-behavior: smooth;
}

::-webkit-scrollbar {
    width: 10px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: var(--grey)
}

::-webkit-scrollbar-thumb {
    background: var(--purple);
    border-radius: 5px;
}

.home {
	display: flex;
	justify-content: center;
	margin-left: auto;
	margin-right: auto;
	max-width: 1700px;
}

.homeImage {
	width: 100%;
	z-index: -1;
	margin-bottom: -200px;
	-webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
	        mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.homeRow {
	display: flex;
	z-index: 1;
	margin-left: 5px;
	margin-right: 5px;
}

@media screen and (max-width: 768px) {
	.homeImage {
		display: none;
	}

	.homeRow {
		flex-direction: column;
	}
}

.product {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-end;
	margin: 10px;
	padding: 20px;
	width: 100%;
	max-height: 400px;
	border-radius: 5px;
	background-color: var(--grey);
	z-index: 1;
}

.productRating {
	display: flex;
}

.productRating > i {
	padding: 3px;
	color: #f0c14b;
}

.product img {
	max-height: 200px;
	width: 100%;
	object-fit: contain;
	margin: 15px 0;
}

.product > button {
	background: var(--gradient);
	border: none;
	padding: 10px;
	border-radius: 25px;
	margin-top: 10px;
	color: var(--white);
}

.product > button:hover {
	cursor: pointer;
	background: var(--purple);
}

.productPrice {
	margin-top: 5px;
}

.productInfo {
	margin-bottom: 15px;
	text-align: left;
	width: 100%;
}

@media screen and (max-width: 768px) {
	.product {
		width: calc(100% - 60px);
		max-height: unset;
	}
}

.link {
	text-decoration: none;
	color: var(--light);
}

.header {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	background: var(--grey);
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 100;
}

.headerLogo {
	object-fit: contain;
	margin: 0 20px;
}

.headerSearchContainer {
	display: flex;
	flex: 1 1;
	align-items: center;
}

.headerSearchInput {
	height: 12px;
	padding: 10px;
	margin: 0 10px;
	border: none;
	width: 100%;
	border-radius: 24px;
	background: var(--dark);
	color: var(--head-light);
}

.headerSearchIcon {
	padding: 5px 8.5px;
	margin: 0 10px;
	line-height: 22px;
	display: block;
	text-align: center;
	color: var(--white);
	background: var(--gradient);
	border-radius: 24px;
	width: 22px;
}

.headerOptionLineOne {
	font-size: 10px;
}

.headerOptionLineTwo {
	font-size: 13px;
	font-weight: 800;
}

.headerOptionBasket {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 5px;
}

.headerBasketCount {
	margin-left: 10px;
}

.headerNavContainer {
	display: flex;
	justify-content: space-evenly;
}

.headerOption {
	display: flex;
	flex-direction: column;
	margin-left: 10px;
	margin-right: 10px;
}

.headerTheme {
	cursor: pointer;
	color: var(--white);
	background: var(--gradient);
	margin: 10px;
	float: right;
	border-radius: 50%;
	font-size: 1.2em;
	font-weight: 900;
	transition: 200ms ease-in-out;
	z-index: 10;
}

.headerTheme i {
	padding: 7px 7px;
}

.headerSearchIcon:hover,
.headerOption:hover,
.headerOptionBasket:hover {
	cursor: pointer;
}

@media screen and (max-width: 768px) {
	.header {
		position: unset;
	}

	.headerNavContainer,
	.headerSearchContainer,
	.headerTitle {
		width: 100vw;
		flex-direction: row;
		margin: 5px 0;
	}

	.break {
		flex-basis: 100%;
		height: 0;
	}

	.headerTheme {
		position: absolute;
		top: 0;
		right: 0;
	}
}

.checkout {
	display: flex;
	align-self: center;
	padding: 20px;
	margin-top: auto;
	background-color: var(--grey);
	height: -webkit-max-content;
	height: max-content;
}

.checkoutLeft {
	margin-right: 10px;
	flex: 1 1;
}

.checkoutLeft > div > h3 {
	word-break: break-all;
}

.checkoutTitle {
	margin-right: 10px;
	padding: 10px;
	border-bottom: 1px solid var(--icon-bg);
}

@media screen and (max-width: 768px) {
	.checkout {
		flex-direction: column;
		text-align: center;
	}
}

.subtotal {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	max-width: 300px;
	height: 60px;
	padding: 20px;
	background-color: var(--dark);
	border-radius: 5px;
}

.subtotalGift {
	display: flex;
	align-items: center;
}

.subtotalGift > input {
	margin-right: 5px;
}

.subtotal > button {
	background: var(--purple);
	border-radius: 25px;
	width: 100%;
	height: 30px;
	border: none;
	margin-top: 10px;
	color: var(--white);
}

@media screen and (max-width: 768px) {
	.subtotal {
		margin: 20px auto;
	}
}

.checkoutProduct {
	display: flex;
	margin-top: 20px;
	margin-bottom: 20px;
}

.checkoutProductInfo {
	padding-left: 20px;
	max-width: 400px;
}

.checkoutProductInfo > button {
	background: var(--gradient);
	padding: 10px;
	border-radius: 25px;
	border: none;
	margin-top: 10px;
	color: var(--white);
}

.checkoutProductImage {
	object-fit: contain;
	width: 180px;
	height: 180px;
}

.checkoutProductRating {
	display: flex;
}

.checkoutProductRating > i {
	padding: 3px;
	color: #f0c14b;
}

.checkoutProductTitle {
	font-size: 17px;
	font-weight: 800;
}

@media screen and (max-width: 768px) {
	.checkoutProduct {
		flex-direction: column;
	}

	.checkoutProductInfo {
		padding: 0px;
		max-width: 400px;
		margin: 0 auto;
		text-align: center;
	}

	.checkoutProductRating {
		margin: 0 auto;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
	}

	.checkoutProductImage {
		margin: 0 auto;
	}
}

.singleProductContainerOuter {
	min-height: calc(100vh - 113px);
	display: flex;
	align-items: center;
	justify-content: center;
}

.singleProductContainer {
	max-width: 500px;
}

.productDescription {
	max-width: 500px; /* Match the width of the product container */
	margin-top: 20px; /* Add space between the product display and the description */
	text-align: justify; /* Justify the text for better readability */
	padding-left: 50px;
	font-size: 15px;
  }
  
  /* If you have more details for the product you could add another class */
  .productDetails {
	max-width: 500px; /* Match the width of the product container */
	/* additional styling */
  }

@media screen and (max-width: 768px) {
	.singleProductContainerOuter {
		min-height: calc(100vh - 190px);
	}
}


.footer {
	height: 60px;
	width: 100%;
	background: var(--grey);
	display: flex;
	justify-content: center;
	flex-direction: column;
	z-index: 6;
}

.footer p {
	line-height: 24px;
	font-size: 14px;
	padding: 0;
	color: var(--head-light);
	text-align: center;
}

.footer a {
	color: var(--purple);
	text-decoration: none;
}


