.home {
	display: flex;
	justify-content: center;
	margin-left: auto;
	margin-right: auto;
	max-width: 1700px;
}

.homeImage {
	width: 100%;
	z-index: -1;
	margin-bottom: -200px;
	mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.homeRow {
	display: flex;
	z-index: 1;
	margin-left: 5px;
	margin-right: 5px;
}

@media screen and (max-width: 768px) {
	.homeImage {
		display: none;
	}

	.homeRow {
		flex-direction: column;
	}
}
